<template>
  <div class="slide_editor">
    <div class="params_area border-bottom">
      <button
          @click="logState"
      >Log vuex state
      </button>
    </div>

    <div class="slide_controls_area">
      <div class="slide_area">
        <RatioKeeper
            :ratio="16 / 9"
            :min-height="660"
        >
          <Slide/>
        </RatioKeeper>
      </div>

      <div class="controls_area border-start">
        <button
            @click="addText"
        >
          Text
        </button>

        <button
            disabled
            @click="addImage"
        >
          Image
        </button>

        <button
            disabled
            @click="addYouTube"
        >
          YouTube
        </button>

        <!--        <div class="text-center">stateId = {{ $store.state.stateId }}</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions} from "vuex";
import {ADD_TEXT_ELEMENT, ADD_IMAGE_ELEMENT, ADD_YOUTUBE_ELEMENT} from "@/store/action-types";
import RatioKeeper from "@/components/utils/RatioKeeper";
import Slide from "@/components/Slide";

export default {
  name: 'SlideEditor',
  components: {Slide, RatioKeeper},
  props: {},
  computed: {},
  methods: {
    ...mapActions({
      // addText: ADD_TEXT_ELEMENT,
      addImage: ADD_IMAGE_ELEMENT,
      addYouTube: ADD_YOUTUBE_ELEMENT,
    }),
    addText() {
      this.$store.dispatch(ADD_TEXT_ELEMENT, {x: 0, y: 0, width: 10, height: 14})
    },
    logState() {
      console.log(this.$store.state)
    }
  },
}
</script>

<style lang="scss">
.slide_editor {
  display: flex;
  flex-direction: column;
  overflow: hidden;

  .params_area {
    height: 80px;
  }

  .slide_controls_area {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;

    .slide_area {
      flex: 1 1 auto;
      overflow: auto;
      background-color: #f1f2f5;

      & > * > div.slide_item {
        box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);
      }
    }

    .controls_area {
      width: 100px;
      background-color: #f8f9fb;

      display: flex;
      flex-direction: column;

      button {
        margin: 8px;

        &:first-child {
          margin-top: 16px;
        }
      }
    }
  }
}
</style>
