import Vue from 'vue'
import Vuex from 'vuex'
import {
    ADD_SLIDE,
    SET_SLIDE_SIZE,
    ADD_SLIDE_ELEMENT,
    DELETE_SLIDE_ELEMENT,
    SET_ACTIVE_SLIDE,
    MOVE_SLIDE_ELEMENT, RESIZE_SLIDE_ELEMENT
} from './mutation-types'
import {ADD_TEXT_ELEMENT, ADD_IMAGE_ELEMENT, ADD_YOUTUBE_ELEMENT} from './action-types'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        newSlideId: 1,
        newSlideElementId: 1,

        slideParams: {
            width: 16,
            height: 9,
            cellSize: 0,
        },

        activeSlideId: null,
        slides: [],

        slideElements: [
            // {
            //     id: 1,
            //     x: 0,
            //     y: 0,
            //     width: 10,
            //     height: 6,
            //     content: {type: 'text', html: '<i>123</i> <b>321</b>'}
            // },
        ]
    },
    getters: {
        activeSlide: (state) => {
            return state.slides.find(slide => slide.id === state.activeSlideId)
        },
        activeSlideElements: (state, getters) => {
            return getters.activeSlide.slideElements
        },
    },
    mutations: {
        increaseNewSlideId(state) {
            state.newSlideId++
        },
        increaseNewSlideElementId(state) {
            state.newSlideElementId++
        },

        [ADD_SLIDE](state) {
            let newSlide = {id: state.newSlideId, slideElements: []}
            state.slides.push(newSlide)
            state.activeSlideId = newSlide.id
        },
        [SET_ACTIVE_SLIDE](state, id) {
            state.activeSlideId = id
        },

        [SET_SLIDE_SIZE](state, params) {
            state.slideParams = Object.assign({}, params)
        },
        [ADD_SLIDE_ELEMENT](state, params) {
            let activeSlideElements = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
            activeSlideElements.push({id: state.newSlideElementId, ...params})
        },
        [DELETE_SLIDE_ELEMENT](state, id) {
            let activeSlideElements = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
            let indexOfElement = activeSlideElements.map(el => el.id).indexOf(id)
            activeSlideElements.splice(indexOfElement, 1)
        },

        [MOVE_SLIDE_ELEMENT](state, {id, x, y}) {
            let slideElement = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
                .find(el => el.id === id)
            slideElement.x = x
            slideElement.y = y
        },
        [RESIZE_SLIDE_ELEMENT](state, {id, width, height}) {
            let slideElement = state.slides.find(slide => slide.id === state.activeSlideId).slideElements
                .find(el => el.id === id)
            slideElement.width = width
            slideElement.height = height
        },
    },
    actions: {
        [ADD_TEXT_ELEMENT]({state, commit}, params) {
            commit(ADD_SLIDE_ELEMENT, {
                ...params,
                content: {component: 'TextObject', html: `<b>${state.stateId}</b>`},
            })
        },
        // eslint-disable-next-line no-unused-vars
        [ADD_IMAGE_ELEMENT]({commit}) {
        },
        // eslint-disable-next-line no-unused-vars
        [ADD_YOUTUBE_ELEMENT]({commit}) {
            // commit(ADD_SLIDE_ELEMENT, {
            //     component: 'YouTubeObject',
            //     html: `<iframe src="https://www.youtube.com/embed/5P-ndTBfyeY?controls=0&autoplay=1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`
            // })
        },
    },
    modules: {},
    strict: process.env.NODE_ENV !== 'production',
})
