<template>
  <div
      ref="movable"
      class="movable"
      :class="{focused: isFocused, selected: isSelected, outside: isOutside, inactive: !isFocused && !isSelected}"
      @dblclick="onDoubleClick"
      :tabindex="element.id"
      @focusin="isFocused = true"
      @focusout="isFocused = false; isSelected = false"
      @keyup="onKeyUp"
  >
    <div>
      <b>{{ element.id }}</b>
      <br>{{ position }}
      <br>{{ size }}
      <br>isFocused: {{ isFocused }}
      <br>isSelected: {{ isSelected }}
      <br>isMoving: {{ isMoving }}
      <br>isOutside: {{ isOutside }}
    </div>

    <template v-if="isFocused">
      <div class="size_change top" ref="top"></div>
      <div class="size_change right" ref="right"></div>
      <div class="size_change bottom" ref="bottom"></div>
      <div class="size_change left" ref="left"></div>
    </template>

    <!--    <component :is="content.component" v-bind="content"/>-->
  </div>
</template>

<script>
import TextObject from "@/components/slide-elements/TextObject";
import YouTubeObject from "@/components/slide-elements/YouTubeObject";
import {DELETE_SLIDE_ELEMENT, MOVE_SLIDE_ELEMENT, RESIZE_SLIDE_ELEMENT} from "@/store/mutation-types";
import {mapMutations} from "vuex";

export default {
  name: 'SlideElement',
  components: {
    // eslint-disable-next-line vue/no-unused-components
    TextObject,
    // eslint-disable-next-line vue/no-unused-components
    YouTubeObject,
  },
  props: ['element', 'slideWidth', 'slideHeight', 'cellSize'],
  data() {
    return {
      isFocused: false,
      isSelected: false,
      isMoving: false,

      defaultZ: '20',
      activeZ: '21',

      x: 0,
      y: 0,
      width: 0,
      height: 0,
    }
  },
  computed: {
    position() {
      return [this.x, this.y]
    },
    size() {
      return [this.width, this.height]
    },
    isOutside() {
      return this.x < 0
          || this.y < 0
          || this.x + this.width > this.slideWidth
          || this.y + this.height > this.slideHeight
    }
  },
  watch: {
    position() {
      this.moveSlideElement({
        id: this.element.id,
        x: this.x,
        y: this.y,
      })

      this.$refs.movable.style.left = this.x * this.cellSize + 'px'
      this.$refs.movable.style.top = this.y * this.cellSize + 'px'
    },
    size() {
      this.resizeSlideElement({
        id: this.element.id,
        width: this.width,
        height: this.height,
      })

      this.$refs.movable.style.width = this.width * this.cellSize + 'px'
      this.$refs.movable.style.height = this.height * this.cellSize + 'px'
    },
    isFocused(newVal) {
      if (newVal) {
        setTimeout(() => {
          this.initializeSizeChanger(this.$refs.top, false, true)
          this.initializeSizeChanger(this.$refs.right, true, false)
          this.initializeSizeChanger(this.$refs.bottom, false, false)
          this.initializeSizeChanger(this.$refs.left, true, true)
        }, 100)
      }
    }
  },
  methods: {
    ...mapMutations({
      moveSlideElement: MOVE_SLIDE_ELEMENT,
      resizeSlideElement: RESIZE_SLIDE_ELEMENT,
    }),

    getPosition() {
      return {x: this.x, y: this.y}
    },
    getSize() {
      return {width: this.width, height: this.height}
    },

    moveSelfInside() {
      if (this.isOutside) {
        if (this.x < 0) this.x = 0
        if (this.y < 0) this.y = 0
        if (this.x + this.width > this.slideWidth) this.x = this.slideWidth - this.width
        if (this.y + this.height > this.slideHeight) this.y = this.slideHeight - this.height
      }

      // still outside
      if (this.isOutside) {
        if (this.width > this.slideWidth) this.width = this.slideWidth
        if (this.height > this.slideHeight) this.height = this.slideHeight
        this.moveSelfInside()
      }
    },

    initializeMovable() {
      let el = this.$refs.movable
      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        this.isMoving = true
        el.style.zIndex = this.activeZ
        let initialX = event.clientX, initialY = event.clientY

        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedCellsX = Math.round((event.clientX - initialX) / this.cellSize)
          let movedCellsY = Math.round((event.clientY - initialY) / this.cellSize)

          this.x = posBeforeMove.x + movedCellsX
          this.y = posBeforeMove.y + movedCellsY
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          this.moveSelfInside()

          this.isMoving = false
          el.style.zIndex = this.defaultZ
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    initializeSizeChanger(el, isHorizontal, shouldChangePos) {
      if (el === undefined) return

      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        event.stopPropagation()

        this.isMoving = true
        el.style.zIndex = this.activeZ
        let initialX = event.clientX, initialY = event.clientY

        let sizeBeforeMove = this.getSize()
        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedCellsX = Math.round((event.clientX - initialX) / this.cellSize)
          let movedCellsY = Math.round((event.clientY - initialY) / this.cellSize)

          if (isHorizontal) {
            if (shouldChangePos) {
              this.x = posBeforeMove.x + movedCellsX
              this.width = sizeBeforeMove.width - movedCellsX
            } else {
              this.width = sizeBeforeMove.width + movedCellsX
            }
          } else {
            if (shouldChangePos) {
              this.y = posBeforeMove.y + movedCellsY
              this.height = sizeBeforeMove.height - movedCellsY
            } else {
              this.height = sizeBeforeMove.height + movedCellsY
            }
          }
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          this.moveSelfInside()

          this.isMoving = false
          el.style.zIndex = this.defaultZ
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    onKeyUp(event) {
      if (event.key === 'Delete')
        this.$store.commit(DELETE_SLIDE_ELEMENT, this.element.id)
    },

    onDoubleClick() {
      this.isSelected = !this.isSelected;
    },
  },
  created() {
    Object.entries(this.element).forEach(([k, v]) => this[k] = v)
  },
  mounted() {
    this.initializeMovable()
  },
}
</script>

<style scoped lang="scss">
@import "src/assets/styles/colors";

$defaultZ: 20;

@mixin visible_border() {
  border: 2px rgba(217, 217, 217, 1) dashed;
}

@mixin invisible_border() {
  border: 2px transparent dashed;
}

.movable {
  position: absolute;
  z-index: $defaultZ;

  @include visible_border;
  background-color: white;
  cursor: move;
  user-select: none;
  outline: none;

  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    @include invisible_border;
    outline: 2px $primary solid;
    border-radius: 2px;
  }

  * {
    //pointer-events: none;
  }

  &.outside {
    opacity: .3;
  }

  &.focused {
    //pointer-events: auto;

    @include invisible_border;
    outline: 3px $primary solid;
    border-radius: 2px;
  }

  &.selected {
  }

  .size_change {
    $squareSize: 10px;
    width: $squareSize;
    height: $squareSize;
    background-color: $primary;
    position: absolute;
    z-index: $defaultZ + 1;

    $moverOffset: 4px;

    &.top {
      top: 0;
      transform: translateY(calc(-50% - #{$moverOffset}));
      cursor: ns-resize;
    }

    &.right {
      right: 0;
      transform: translateX(calc(50% + #{$moverOffset}));
      cursor: ew-resize;
    }

    &.bottom {
      bottom: 0;
      transform: translateY(calc(50% + #{$moverOffset}));
      cursor: ns-resize;
    }

    &.left {
      left: 0;
      transform: translateX(calc(-50% - #{$moverOffset}));
      cursor: ew-resize;
    }
  }
}
</style>
