<template>
  <div
      class="content"
      ref="content"
      :style="{ width: contentWidth, height: contentHeight }"
  >
    <slot v-if="ready"></slot>
  </div>
</template>

<script>
export default {
  name: 'RatioKeeper',
  props: {
    ratio: Number,
    minHeight: Number,
  },
  data() {
    return {
      ready: false,
      height: 660,
    }
  },
  computed: {
    contentWidth() {
      return (this.height - 32) * this.ratio + 20 * 2 + 'px'
    },
    contentHeight() {
      return this.height + 'px'
    },
  },
  watch: {
    ratio() {
      this.keepAspectRatio()
    },
  },
  methods: {
    changeSelf(transformation) {
      this.ready = false
      transformation()
      this.ready = true
    },
    keepAspectRatio() {
      let newHeight = this.$refs.content.parentElement.clientHeight - 2
      if (newHeight > this.minHeight) {
        this.height = newHeight
      } else {
        this.height = this.minHeight
      }
    },
  },
  created() {
    // window.addEventListener('resize', () => this.changeSelf(this.keepAspectRatio))
  },
  mounted() {
    this.changeSelf(this.keepAspectRatio)
  },
  destroyed() {
    // window.removeEventListener('resize', () => this.changeSelf(this.keepAspectRatio))
  },
}
</script>

<style scoped lang="scss">
.content {
  width: 100px;
  height: 100px;

  margin: 0 auto;
  padding: 16px 20px;
}
</style>
