    </main>
  </div>
</template>

<script>
import Heading from '@/components/Heading.vue';
import Sidebar from "@/components/Sidebar";
import SlideEditor from "@/components/SlideEditor";

export default {
  name: 'Editor',
  components: {
    SlideEditor,
    Heading,
    Sidebar,
  },
  computed: {
    isAnySlideSelected() {
      return this.$store.getters.activeSlide !== undefined
    }
  },
}
</script>

